import React, { Component, Fragment } from "react";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  FC_GetAllPositions,
  FC_AllSchoolHierarchyDetails,
  SchoolHierarchyStore,
  FC_GetAllSystemRoles,
  FC_LoadSystemUsers,
  FC_Clean_users,
} from "../../actions";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import { StoreState } from "../../reducers";
import { IoMdArrowDropright } from "react-icons/io";
import Loading from "../../components/Loading/Loading";
import {
  SystemUserInfo,
  SystemUsersStore,
} from "../../actions/system_users.action";
import { search } from "../../utils/functions";
import { UserProfile } from "../UserProfile/UserProfile";
import { Link } from "react-router-dom";

interface SystemUsersListProps {
  auth: Auth;
  basic: Basic;
  schoolHierarchyStore: SchoolHierarchyStore;
  systemUsers: SystemUsersStore;
  FC_GetAllSystemRoles: (
    callBack: (status: boolean, msg: string) => void
  ) => void;
  FC_LoadSystemUsers: (
    role_id: string,
    limit: number,
    page_number: number,
    clean: boolean,
    setLoading: (status: boolean) => void
  ) => void;
  FC_Clean_users: () => void;
}
interface SystemUsersListState {
  loading: boolean;
  selected_role_id: string;
  selected_page: number;
  page_limit: number;
  selected_gender: string | null;
  search_value: string;
  selectedUser: SystemUserInfo | null;
}

export class _SystemUsersList extends Component<
  SystemUsersListProps,
  SystemUsersListState
> {
  constructor(props: SystemUsersListProps) {
    super(props);

    this.state = {
      loading: false,
      selected_role_id: "3", //DDE
      selected_page: 1,
      page_limit: 100,
      selected_gender: null,
      search_value: "",
      selectedUser: null,
    };
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };

  nextPage = () => {
    const newNumber = this.state.selected_page + 1;
    if (this.pageNumbersList().length > this.state.selected_page) {
      this.setState({ selected_page: newNumber });
      this.LoadByPage(
        newNumber,
        this.state.page_limit,
        this.state.selected_role_id,
        false
      );
    }
  };

  previousPage = () => {
    const newNumber =
      this.state.selected_page === 1 ? 1 : this.state.selected_page - 1;
    this.setState({
      selected_page: newNumber,
    });
    this.LoadByPage(
      newNumber,
      this.state.page_limit,
      this.state.selected_role_id,
      false
    );
  };

  pageNumbersList = (): number[] => {
    let max =
      this.props.systemUsers.users !== null
        ? parseInt(this.props.systemUsers.users[0].total_users) /
            this.state.page_limit +
          (parseInt(this.props.systemUsers.users[0].total_users) %
            this.state.page_limit >
          0
            ? 1
            : 0)
        : 0;
    let pageNumbers: number[] = [];
    for (let i = 1; i <= max; i++) {
      pageNumbers.push(i);
    }
    return pageNumbers.length === 0 ? [1] : pageNumbers;
  };
  LoadByPage = (
    pageNumber: number,
    limit: number,
    role_id: string,
    clean: boolean
  ) => {
    this.setState({ selected_page: pageNumber });
    if (
      this.props.systemUsers.users === null ||
      this.props.systemUsers.users.find(
        (itm) => itm.page_number === pageNumber
      ) === undefined ||
      clean === true
    ) {
      this.props.FC_LoadSystemUsers(
        role_id,
        limit,
        pageNumber,
        clean,
        this.setLoading
      );
    }
  };

  FilteredData = () => {
    if (this.props.systemUsers.users !== null) {
      let test = this.props.systemUsers.users.find(
        (itm) => itm.page_number === this.state.selected_page
      );
      if (test !== undefined) {
        let users = test.users;
        if (
          this.state.selected_gender !== null &&
          this.state.selected_gender !== ""
        ) {
          users = users.filter((itm) => itm.sex === this.state.selected_gender);
        }
        return search(users, this.state.search_value) as SystemUserInfo[];
      }
    }
    return [];
  };

  componentDidMount = () => {
    if (this.props.basic.roles === null) {
      this.props.FC_GetAllSystemRoles((status: boolean, msg: string) => {});
    }
    this.LoadByPage(
      this.state.selected_page,
      this.state.page_limit,
      this.state.selected_role_id,
      true
    );
  };

  changeRole = (role_id: string) => {
    this.setState({ selected_role_id: role_id, selected_page: 1 });
    this.LoadByPage(1, this.state.page_limit, role_id, true);
  };

  changePageLimit = (page_limit: string) => {
    this.setState({ page_limit: parseInt(page_limit), selected_page: 1 });
    this.LoadByPage(1, parseInt(page_limit), this.state.selected_role_id, true);
  };

  render() {
    if (
      this.props.basic.roles === null ||
      this.props.systemUsers.users === null ||
      this.state.loading === true
    ) {
      return (
        <div className="bg-white rounded-md p-4 flex flex-col items-center justify-center m-5">
          <Loading />
          <div className="text-xl">Loading, please wait...</div>
        </div>
      );
    }
    return (
      <Fragment>
        <div>
          <DashboardContainer>
            {this.state.selectedUser === null ? (
              <div>
                {/* Header */}
                <div className="flex flex-row items-center justify-between gap-3">
                  <div>
                    <span className="text-3xl font-bold">System users</span>
                    <div className="flex flex-row items-center gap-2 -mt-1">
                      <span className="text-base text-gray-500">
                        System users
                      </span>
                      {this.state.selected_role_id !== "" && (
                        <IoMdArrowDropright className="text-2xl -mx-2 text-gray-500" />
                      )}
                      <span className="text-base font-bold text-primary-800 underline">
                        {
                          this.props.basic.roles.find(
                            (itm) => itm.role_id === this.state.selected_role_id
                          )?.role
                        }
                      </span>
                      {this.state.selected_gender !== null &&
                        this.state.selected_gender !== "" && (
                          <IoMdArrowDropright className="text-2xl -mx-2 text-gray-500" />
                        )}
                      {this.state.selected_gender !== null &&
                        this.state.selected_gender !== "" && (
                          <span className="text-base font-bold text-primary-800 underline">
                            {this.state.selected_gender}
                          </span>
                        )}
                    </div>
                  </div>
                  <div>
                    <div className="flex flex-row items-center justify-end gap-3">
                      <div className="text-right">
                        <div className="text-sm text-gray-500">Total users</div>
                        <div className="text-2xl -mt-1 font-bold">
                          {this.props.systemUsers.users !== null &&
                          this.props.systemUsers.users.length > 0
                            ? parseInt(
                                this.props.systemUsers.users[0].total_users
                              ).toLocaleString()
                            : 0}
                        </div>
                      </div>
                      <div>
                        <Link
                          to="/register-administration-staff"
                          className="btn bg-accent-900 hover:bg-yellow-900 text-white cursor-pointer px-3 py-2 rounded font-bold"
                        >
                          Add new staff
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
                {/* Filter section */}
                <div className="mt-3">
                  <div className="text-sm">Filter by:</div>

                  <div className="flex flex-row items-center gap-3 w-full">
                    {/* Page limit */}
                    <div>
                      <div className="font-bold text-sm">Rows</div>
                      <div>
                        <select
                          onChange={(e) => this.changePageLimit(e.target.value)}
                          value={this.state.page_limit}
                          className="border border-gray-400 px-3 py-2 rounded"
                        >
                          {[
                            10, 20, 50, 100, 500, 1000, 5000, 10000, 50000,
                            1000000, 100000000,
                          ].map((limit, l) => (
                            <option key={l + 1} value={limit}>
                              {limit}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                    {/* Filter by User role */}
                    <div>
                      <div className="font-bold text-sm">User role</div>
                      <div>
                        <select
                          onChange={(e) => this.changeRole(e.target.value)}
                          value={this.state.selected_role_id}
                          className="border border-gray-400 px-3 py-2 rounded"
                        >
                          <option value="">Choose user role</option>
                          {this.props.basic.roles.map((role, r) => (
                            <option key={r + 1} value={role.role_id}>
                              {role.role}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                    {/* Filter by Gender */}
                    <div>
                      {console.log("Gender: ", this.state.selected_gender)}
                      <div className="font-bold text-sm">Gender</div>
                      <div>
                        <select
                          value={
                            this.state.selected_gender === null
                              ? ""
                              : this.state.selected_gender
                          }
                          onChange={(e) =>
                            this.setState({
                              selected_gender:
                                e.target.value !== "" ? e.target.value : null,
                            })
                          }
                          className="border border-gray-400 px-3 py-2 rounded"
                        >
                          <option value="">Choose gender</option>
                          <option value="">View by all</option>
                          <option value="MALE">MALE</option>
                          <option value="FEMALE">FEMALE</option>
                        </select>
                      </div>
                    </div>
                    {/* Filter by search */}
                    <div className="w-full">
                      <div className="font-bold text-sm">Search on a page</div>
                      <div className="w-full">
                        <input
                          type="text"
                          onChange={(e) =>
                            this.setState({ search_value: e.target.value })
                          }
                          placeholder="Search keyword..."
                          value={this.state.search_value}
                          className="border border-gray-400 px-3 py-2 rounded w-full"
                        />
                      </div>
                    </div>
                  </div>
                </div>
                {/* Body section */}
                <div className="w-full overflow-x-auto my-5">
                  <table className="min-w-full">
                    <thead>
                      <tr className="border-b-2 border-gray-300 text-sm">
                        <th className="p-1 py-2 text-center">#</th>
                        <th className="p-1 py-2 text-left">Full name</th>
                        <th className="p-1 py-2 text-left">Gender</th>
                        <th className="p-1 py-2 text-left">National ID</th>
                        <th className="p-1 py-2 text-left">Phone</th>
                        <th className="p-1 py-2 text-left">Username</th>
                        <th className="p-1 py-2 text-left">District</th>
                        <th className="p-1 py-2 text-left">School</th>
                        <th className="p-1 py-2"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.props.systemUsers.users === null ? (
                        <tr>
                          <td colSpan={10}>Loading...</td>
                        </tr>
                      ) : this.FilteredData().length === 0 ? (
                        <tr>
                          <td colSpan={10}>
                            <div className="text-center text-2xl py-5 text-gray-600 bg-gray-200 rounded-xl my-4">
                              No result found!
                            </div>
                          </td>
                        </tr>
                      ) : (
                        this.FilteredData().map((item, i) => (
                          <tr
                            key={i + 1}
                            className={`border-b text-sm ${
                              (i + 1) % 2 === 0 ? "bg-gray-100" : ""
                            } hover:bg-yellow-50 group`}
                          >
                            <td className="p-1 py-2 text-center">
                              {this.state.page_limit *
                                this.state.selected_page -
                                this.state.page_limit +
                                (i + 1)}
                            </td>
                            <td className="p-1 py-2 text-left">
                              {item.full_name}
                            </td>
                            <td className="p-1 py-2 text-left">{item.sex}</td>
                            <td className="p-1 py-2 text-left">{item.nid}</td>
                            <td className="p-1 py-2 text-left">
                              {item.phone_numbers}
                            </td>
                            <td className="p-1 py-2 text-left">
                              {item.username}
                            </td>
                            <td className="p-1 py-2 text-left">
                              {item.district_name}
                            </td>
                            <td className="p-1 py-2 text-left">
                              {item.school_name}
                            </td>
                            <td className="p-1 py-2 text-right">
                              <div
                                onClick={() =>
                                  this.setState({ selectedUser: item })
                                }
                                title="View user profile"
                                className="bg-blue-100 rounded px-3 py-2 text-center font-bold cursor-pointer group-hover:bg-primary-800 group-hover:text-white"
                              >
                                View
                              </div>
                            </td>
                          </tr>
                        ))
                      )}
                    </tbody>
                    <tfoot>
                      <tr>
                        <td colSpan={10} className="pt-5">
                          <div className="flex flex-row items-center justify-between gap-3">
                            <div className="text-gray-500">
                              Showing{" "}
                              <span className="text-black">
                                {this.state.page_limit *
                                  this.state.selected_page -
                                  this.state.page_limit +
                                  1}
                              </span>{" "}
                              to{" "}
                              <span className="text-black">
                                {this.state.page_limit *
                                  this.state.selected_page}
                              </span>{" "}
                              of{" "}
                              <span className="text-black font-bold">
                                {this.props.systemUsers.users !== null &&
                                this.props.systemUsers.users.length > 0
                                  ? parseInt(
                                      this.props.systemUsers.users[0]
                                        .total_users
                                    ).toLocaleString()
                                  : 0}
                              </span>{" "}
                              results
                            </div>
                            <div className="flex flex-row items-center">
                              <div
                                onClick={() => this.previousPage()}
                                className="bg-gray-100 rounded-l-md border border-gray-300 hover:border-primary-800 py-2 px-3 font-bold text-gray-800 hover:text-white hover:bg-primary-800 cursor-pointer"
                              >
                                Previous
                              </div>
                              <div>
                                <select
                                  value={this.state.selected_page}
                                  onChange={(e) =>
                                    this.LoadByPage(
                                      parseInt(e.target.value),
                                      this.state.page_limit,
                                      this.state.selected_role_id,
                                      false
                                    )
                                  }
                                  className="border border-gray-400 px-3 py-2 overflow-x-auto flex flex-row items-center text-center justify-center font-extrabold"
                                >
                                  {this.pageNumbersList().map((page, p) => (
                                    <option key={p + 1} value={page}>
                                      {page}
                                    </option>
                                  ))}
                                </select>
                              </div>
                              <div
                                onClick={() => this.nextPage()}
                                className="bg-gray-100 rounded-r-md border border-gray-300 hover:border-primary-800 py-2 px-3 font-bold text-gray-800 hover:text-white hover:bg-primary-800 cursor-pointer"
                              >
                                Next
                              </div>

                              {/* <div className="w-72 overflow-x-auto flex flex-row items-center">
                            {this.pageNumbersList().map((page, p) => (
                              <div
                                key={p + 1}
                                onClick={() =>
                                  this.LoadByPage(
                                    page,
                                    this.state.page_limit,
                                    this.state.selected_role_id,
                                    false
                                  )
                                }
                                className={`border-t border-b px-4 py-2 cursor-pointer ${
                                  this.state.selected_page === page
                                    ? "bg-primary-800 text-white animate__animated animate__zoomIn animate__faster"
                                    : "hover:bg-blue-100 hover:text-primary-800 "
                                } font-bold`}
                              >
                                {page}
                              </div>
                            ))}
                          </div> */}
                            </div>
                          </div>
                        </td>
                      </tr>
                    </tfoot>
                  </table>
                </div>
              </div>
            ) : (
              <UserProfile
                user_id={
                  this.state.selectedUser.staff_code === null
                    ? undefined
                    : this.state.selectedUser.staff_code
                }
                isExternal={true}
                user_info={
                  this.state.selectedUser.user_id === null ||
                  this.state.selectedUser.username === null
                    ? undefined
                    : {
                        user_id: this.state.selectedUser.user_id,
                        username: this.state.selectedUser.username,
                      }
                }
                close={() => this.setState({ selectedUser: null })}
              />
            )}
          </DashboardContainer>
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  schoolHierarchyStore,
  systemUsers,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  schoolHierarchyStore: SchoolHierarchyStore;
  systemUsers: SystemUsersStore;
} => {
  return {
    auth: auth,
    basic: basic,
    schoolHierarchyStore: schoolHierarchyStore,
    systemUsers: systemUsers,
  };
};

export const SystemUsersList = connect(mapStateToProps, {
  FC_GetAllPositions,
  FC_AllSchoolHierarchyDetails,
  FC_GetAllSystemRoles,
  FC_LoadSystemUsers,
  FC_Clean_users,
})(_SystemUsersList);
